<template>
	<div class="container">
    <h1>Todos</h1>
    <b-alert v-model="message"
    				 class="alert"
    				 variant="success" 
    				 show>
    				 {{ message }}
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </b-alert>  
  </div>
</template>
