<template>
	<li>
		<span class="one">
			<input type="checkbox" @change="todo.completed = !todo.completed">
			<strong> {{ index+1+"." }} </strong>
			<strong v-bind:class="{done: todo.completed}">{{ todo.title }}</strong>
			<span class="ones" v-if="todo.completed == false">Не выполненно</span>
			<span class="ones" v-else>Выполнено</span>
		</span>
		<button class='rm' @click="$emit('remove-todo', todo.id)">X</button>
	</li>
</template>
<script>
export default {
	props: {
		todo:{
			type: Object,
			required: true
		},
		index: Number
	}
};
</script>

<style scoped>
	li {
		border:1px solid #ccc;
		display: flex;
		justify-content: space-between;
		padding: .5rem 2rem;
		margin-bottom: 1rem;
	}
	.rm {
		background: red; 
	}
	.done {
		text-decoration: line-through;
	}
	.one {
		padding: 1px;
	}
	.ones {
		margin-left: 20px;
	}
</style>