<template>
  <div id="app">
    <router-view/>
  </div>
</template>
<script>
import TodoList from '@/components/TodoList.vue'
import AddTodo from '@/components/AddTodo.vue'
export default {
  name: 'app',
  props:['title'],
  data() {
    return {
      todos: [
        {id: 1, title: 'Покушать', completed: false},
        {id: 2, title: 'Помыть посуду', completed: false},
        {id: 3, title: 'Лечь спать', completed: false},
      ]
    }
  },
  methods: {
    removeTodo(id) {
      this.todos = this.todos.filter(t => t.id !== id)
    },
    addTodo(todo) {
      this.todos.push(todo)
    },
  },
  watch: {
    title: {
      handler() {
        console.log('todo_items');
        localStorage.setItem('title', JSON.stringify(this.title));
      },
      deep: true,
    }
  },
  components: {
    TodoList, AddTodo
  },
};
</script>

