<template>
	<div>
		<div v-if="isVisible">	
			<b-alert v-model="message"
  				 		 class="alert"
  				 		 variant="success" 
  				 		 show>
  				 		 {{ message }}
  		<button type="button" class="close" data-dismiss="alert" aria-label="Close">
  		  <span aria-hidden="true" @click="outisVisible">&times;</span>
  		</button>
  		</b-alert>
  	</div>  
		<form @submit.prevent="onSubmit">
			<input type="text" v-model="title">
			<button type="submit"@click="inisVisible">Добавить Задачу</button>
		</form>
	</div>
</template>

<script>
import Alert from '@/components/Alert.vue';	
import uuid from 'uuid';
export default {
	data() {
		return {
			title: '',
			message: '',
			isVisible: true,
		}
	},

	methods: {
		onSubmit() {
			if (this.title.trim()) {
				const newTodo = {
					id: uuid(),
					title: this.title,
					completed: false,
				}
				this.$emit('add-todo', newTodo)
				this.title = ''
				this.message = `Задача добавлена`;

			}
		},
		outisVisible() {
			this.isVisible = false
		},
		inisVisible() {
			this.isVisible = true
		}
		
	},

};
</script>

<style scoped>
	input{
		width: 500px;
	}

	.alert {
		min-height: 50px;
	}

	.isVisible {
		display: flex;
		justify-content: center;
		min-height: 50px;
		max-width: 500px;
	}
	.done {
		display: none;
	}
</style>