<template>
	<div class="conteiner">
		<h2>Здравствуйте, представьтесь пожалуйста</h2>
		<span>
			<input type="text"
				   placeholder="Имя"
				   v-model="name">
			<button><router-link to="/todos">Далее</router-link></button>
		</span>
	</div>
</template>
<script>
export default {
	data() {
		return {
			name: ""
		}
	},
	mounted() {
		if (localStorage.name) {
			this.name = localStorage.name;
		}
	},
	watch: {
		name(newName) {
			localStorage.name = newName;
		}
	}	
}
</script>
<style>
	.conteiner {
		padding: 10rem 2rem;
	}
</style>